/* eslint-disable */
<template>
  <v-col cols="12" sm="6">
    <v-card>
      <v-app-bar flat color="rgba(0,0,0,0)">
        <v-toolbar-title :title="title"> </v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn :color="color" class="white--text mt-2">Weekly</v-btn>
        <v-btn color="white" class="dark mt-2">Monthly</v-btn>
      </v-app-bar>
      <v-sparkline
        :line-width="width"
        :padding="padding"
        :smooth="radius || false"
        :value="value"
        auto-draw
        :color="color"
      >
        <template v-slot:label="item"> {{ item.value }} </template>
      </v-sparkline>
    </v-card>
  </v-col>
</template>
<script>
export default {
  name: "chart",
  props: ["color", "title"],
  data() {
    return {
      tab: null,
      text: "center",
      fill: true,
      padding: 8,
      radius: 10,
      value: [0, 2, 5, 9, 5, 10, 3, 5, 0, 1, 8, 2, 9, 0],
      width: 2,
      lineCap: "round",
      autoLineWidth: false,
      fills: false,
    };
  },
};
</script>
<style lang="sass" scoped>
.v-card-on-hover.theme--dark
  backdrop-color: rgba(#FFF, 0.8)
>.v-card-text
  color: #000
</style>
<style scoped>
.border {
  border-right: 1px solid grey;
}
</style>