<template>
  <v-col cols="12" sm="3" md="3">
    <v-responsive max-width="400" class="mx-auto mb-4">
      <div class="text-center">
        <v-menu>
          <template v-slot:activator="{ on: menu, attrs }">
            <v-tooltip bottom>
              <template v-slot:activator="{ on: tooltip }">
                <v-text-field
                  v-bind="attrs"
                  v-on="{ ...tooltip, ...menu }"
                  type="scroll"
                  label="Clients"
                ></v-text-field>
              </template>
              <span>Im A ToolTip</span>
            </v-tooltip>
          </template>
          <v-list>
            <v-list-item v-for="(item, index) in items" :key="index">
              <v-list-item-title>{{ item.title }}</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </div>
    </v-responsive>
  </v-col>
</template>
<script>
export default {
  data: () => ({
    items: [
      { title: "Client 1" },
      { title: "Client 2" },
      { title: "Client 3" },
      { title: "Client 4" },
    ],
  }),
};
</script>