<template>
  <div >
    <div class="background"></div>
    <div class="container">
      <v-snackbar
        top
        right
        class="pa-10 mr-16 ml-2"
        color="cyan"
        v-model="snackbar"
        :timeout="-1"
        :value="true"
        shaped
      >
        Login successfully
      </v-snackbar>
    </div>
    <v-main class="pa-md-16 mx-lg-12">
      <v-col cols="6" lg="4" class="ml-auto" left>
        <v-card class="pa-4">
          <div class="text-center">
            <v-avatar size="100" color="cyan lighten-4">
              <v-icon size="60" color="cyan">mdi-account</v-icon>
            </v-avatar>
            <h2 class="cyan--text">User Login page</h2>
          </div>
          <v-form @submit.prevent="submitHandler" ref="form">
            <v-card-text
              ><v-text-field
                v-model="email"
                :rules="emailRules"
                type="email"
                label="Email"
                placeholder="Email"
                prepend-inner-icon="mdi-account"
              />
              <v-text-field
                v-model="password"
                :rules="passwordRules"
                :type="passwordShow ? 'text' : 'password'"
                label="Password"
                placeholder="Password"
                prepend-inner-icon="mdi-key"
                :append-icon="passwordShow ? 'mdi-eye' : 'mdi-eye-off'"
                @click:append="passwordShow = !passwordShow"
              />
              <v-switch label="Remember" color="cyan"></v-switch>
            </v-card-text>
            <v-card-actions class="justify-center">
              <v-btn
                type="submit"
                :loading="loading"
                color="cyan"
                dark
                class="px-8"
              >
                Login</v-btn
              >
            </v-card-actions>
          </v-form>
        </v-card>
      </v-col>
    </v-main>
  </div>
</template>
<script>
export default {
  name: "Login",
  data() {
    return {
      sidebar: false,
      loading: false,
      snackbar: false,
      passwordShow: false,
      password: "",
      passwordRules: [
        (v) => !!v || "Password is required",
        (v) => (v && v.length >= 6) || "Name must be less than 6 characters",
      ],
      email: "",
      emailRules: [
        (v) => !!v || "E-mail is required",
        (v) => /.+@.+\..+/.test(v) || "E-mail must be valid",
      ],
    };
  },
  methods: {
    submitHandler: function () {
      if (this.$refs.form.validate()) {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.snackbar = true;
          setTimeout(() => this.redirect(), 2000);
        }, 1000);
      }
    },
    redirect() {
      this.$router.push({ path: "/home", name: "Home" });
    },
  },
};
</script>
<style>
.background {
  background-image: url(../assets/cake.jpg);
  height: 100%;
  width: 55%;
  display: block;
  position: absolute;
  top: 0;
  background-size: cover;
}
</style>
